}

.choices__inner {
  vertical-align: top;
  width: 100%;
  min-height: 44px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 2.5px;
  padding: 7.5px 7.5px 3.75px;
  font-size: 14px;
  display: inline-block;
  overflow: hidden;
}

.is-focused .choices__inner, .is-open .choices__inner {
