.hero__title {
  font-family: 'Roboto';
  font-style: normal;
}

.hero__get-catalog {
  text-decoration: none;

  color: #111111;
}

@media screen and (min-width: $container-width-mobile) {
  .hero {
    background-image: linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
      url('/src/images/mobile-heroy.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .hero__bagraundTwo {
    padding-top: 40px;
    padding-bottom: 84px;
  }
}
@media screen and (min-width: $container-width-tablet) {
  .hero {
    background-image: linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
      url('/src/images/tabllet-heroy.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .hero__bagraundTwo {
    background-image: url('/src/images/blak-tablet.png');
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 62px;
    padding-bottom: 62px;
  }
}
@media screen and (min-width: $container-width-desktop) {
  .hero {
    background-image: linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
      url('/src/images/gesk-heroy.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .hero__bagraundTwo {
    background-image: url('/src/images/blak-desk.png');
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 169px;
    padding-bottom: 169px;
  }
}

@media screen and (min-width: $container-width-mobile) {
  .hero__info {
    max-width: 199px;
  }
  .hero__title-color {
    color: #fff;
  }
  .hero__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 1.18;
    margin-bottom: 8px;
  }
  .hero__title-br {
    display: none;
  }

  .hero__text {
    color: #f8f8f8;
    font-size: 14px;
    line-height: 1.14;
    margin-bottom: 16px;
  }
  .hero__spanhiden {
    display: none;
  }
}
@media screen and (min-width: $container-width-tablet) {
  .hero__info {
    max-width: 315px;
  }
  .hero__title-color {
    color: var(--color-titleAll);
  }
  .hero__title {
    font-weight: 700;
    font-size: 44px;
    line-height: 1.18;
    margin-bottom: 8px;
  }
  .hero__title-br {
    display: block;
  }
  .hero__text {
    color: var(--color-heroPage);
    font-size: 14px;
    line-height: 1.14;
    margin-bottom: 32px;
  }
  .hero__spanhiden {
    display: block;
  }
}
@media screen and (min-width: $container-width-desktop) {
  .hero__info {
    max-width: 464px;
  }
  .hero__title-color {
    color: var(--color-titleAll);
  }
  .hero__title {
    font-weight: 700;
    font-size: 64px;
    line-height: 1.17;
    margin-bottom: 16px;
  }
  .hero__text {
    color: var(--color-heroPage);
    font-size: 16px;
    line-height: 1.18;
  }
}

@media screen and (min-width: $container-width-tablet) {
  .hero-white {
    background-image: url('/src/images/SubtractWhite.png');
  }
}
@media screen and (min-width: $container-width-desktop) {
  .hero-white {
    background-image: url('/src/images/desk-white.png');
  }
}
