.backdrop {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  overflow: hidden;

  visibility: visible;
  opacity: 1;

  background-color: var(--bagrBagdrop);

  transition: visibility var(--transition-duration) var(--transition-timing-function),
    opacity var(--transition-duration) var(--transition-timing-function);
}

.backdrop--hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  overflow: auto;
}

.backdrop__modal {
  position: absolute;
  top: 50%;
  left: 50%;
}
